	module.exports = {"data":{"markdownRemark":{"html":"<p>이른 아침 국제 정치 학회 (International Politics Society)의 \"참여 (PARTICIPATION)\"주제는 \"민주화\"라는 주제로 열린 이번 국제 회의의 주제와 보고서를 보았다. 오늘날 세계에서 민주화 문제는 점차 중요 해지고 있으며 사회 발전의 기본 방향이기도합니다. 올해의 국제 컨퍼런스는 베를린에서 열려 있습니다. 중국 정치 학회는 국제 정치 학회 (International Political Society)에서 탈퇴했다. 주제가 좋다. 중국에서도 공부해야한다.</p>\n<p>또한 나는 중국 정치 학회 (China Political Society)의 \"정치적 안정성\"이라는 논문을 읽었으며 많은 좋은 기사는 없다. 부서에서 기사를 발표 한 몇몇 대학원생이 있습니다.</p>","id":"/home/swain/workspace/political-life/src/pages/ko/0016.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/ko/0016.md","frontmatter":{"title":"1월 24일 월요일"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/ko/0016.md absPath of file >>> MarkdownRemark"}}

